.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    /* width: 70%; */
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    border-radius: 7px;
    width: auto;
    
}

@media (min-width: 600px) {
    .Modal {
        width: auto;
        
    }
}